import React from 'react';
import { observer } from 'mobx-react-lite';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import appsMobx from '../../../../mobx/appsMobx';

const statusList = [
    'Готов',
    'Отклон',
    'Модерация',
]

function TransferModerationStatus({app}) {  
    const [stat, setStat] = React.useState(app.transferStatus);

    React.useEffect(() => {
        if(stat){
            fetch('/api/changeTransferModerationStatus', {
                method:'PUT',
                headers: {
                  "Content-Type": "application/json",
                },
                body: JSON.stringify({ 
                  id: app._id,
                  transferStatus: stat,
                  })
              })
              .then(res => res.json())
              .then(data => {
                if(data.result){
                    appsMobx.update()
                }
              })
              .catch(err => console.log(err))
        }
      }, [stat])

    const handleChange = (event) => {
        setStat(event.target.value);
    };
  
    return (
      <Box sx={{ minWidth: 120 }}>
        <FormControl fullWidth>
          <InputLabel id="demo-simple-select-label">Статус</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={stat}
            label="Статус"
            onChange={handleChange}
          >
            {statusList.map(status => <MenuItem value={status}>{status}</MenuItem>)}
          </Select>
        </FormControl>
      </Box>
    );
}

export default observer(TransferModerationStatus);
