import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import AddNewAccount from './addNew/AddNewAccount';
import trustAccountsMobx from '../../../mobx/trustAccountsMobx';
import { observer } from 'mobx-react-lite';


function TrustAccounts() {

  const renderStatusColor = (row) => {
    let color = ''
    switch(row.status){
      case 'Бан':
        color = 'red'; break;
      case 'Готов':
        color = 'green'; break;
      case 'Трансфер':
        color = 'yellow'; break;
        case 'Модерация':
          color = 'yellow'; break;
      default:
        color = ''
    }
    return color
  }

  return (<>
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Аккаунт Индиго</TableCell>
            <TableCell align="left">Имя разработчика</TableCell>
            <TableCell align="left">Почта</TableCell>
            <TableCell align="left">ID разработчика</TableCell>
            <TableCell align="left">ID транзакций</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {trustAccountsMobx.list.map((row) => (
            <TableRow
              style={{backgroundColor: renderStatusColor(row)}}
              key={row.title}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">{row.title}</TableCell>
              <TableCell align="left" style={{backgroundColor: renderStatusColor(row)}}>{row.developer}</TableCell>
              <TableCell align="left" style={{backgroundColor: renderStatusColor(row)}}>{row.email}</TableCell>
              <TableCell align="left" style={{backgroundColor: renderStatusColor(row)}}>{row.developerId}</TableCell>
              <TableCell align="left" style={{backgroundColor: renderStatusColor(row)}}>{row.transactionID}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    <AddNewAccount />
    </>
  );
}
export default observer(TrustAccounts);
