import {action, makeObservable, observable} from 'mobx';

class TrustAccountsMobx {
  list = [];

  constructor() {
    makeObservable(this, {
        list: observable,
        change: action,
    });
  }

  update(){
    fetch('/api/getTrustAccounts')
        .then(res => res.json())
        .then(data =>{
          this.change(data)
        })
        .catch(err => console.log(err))
  }

  change(arr) {
    this.list = arr
    console.log(this.list);
    
  }
}

const trustAccountsMobx = new TrustAccountsMobx();

export default trustAccountsMobx;