import React from 'react';
import { observer } from 'mobx-react-lite';
import TransferModerationStatus from './TransferModerationStatus';

function TransferModeration({app}) {

  const render = () => {
    if(app.transferStatus == 'Готов') {
      return <div>Готово</div>
    }
    if(app.transferAccount) {
      return <TransferModerationStatus app={app} />
    }
  }

  return (
    <div>
      {render()}
    </div>
  );
}

export default observer(TransferModeration);
