import * as React from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import selfAccountsMobx from '../../../../mobx/selfAccountsMobx';
import appsMobx from '../../../../mobx/appsMobx';

function ChooseTransferAccount({app}) {
  const [acc, setAcc] = React.useState(null);

  React.useEffect(() => {
    if(acc){
        console.log(acc);
        fetch('/api/changeTransferAccount', {
            method:'PUT',
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ 
              id: app._id,
              accountId: acc._id,
              })
          })
          .then(res => res.json())
          .then(data => {
            if(data.result){
                appsMobx.update()
            }
          })
          .catch(err => console.log(err))
    }
  }, [acc])

  const handleChange = (event) => {
    setAcc(event.target.value);
  };

  const renderMenuItems = () => {
    return selfAccountsMobx.list.map(account => {
        return <MenuItem value={account}>{account.title}</MenuItem>
    })
  }

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">Аккаунт</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={acc}
          label="Аккаунт"
          onChange={handleChange}
        >
          {renderMenuItems()}
        </Select>
      </FormControl>
    </Box>
  );
}

export default ChooseTransferAccount