import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import SuccessSnackbar from './SuccessSnackbar';
import selfAccountsMobx from '../../../../mobx/selfAccountsMobx';

export default function AddNewAccount() {
  const [open, setOpen] = React.useState(false);
  const [title, setTitle] = React.useState('')
  const [developer, setDeveloper] = React.useState('')
  const [email, setEmail] = React.useState('')
  const [developerId, setDeveloperId] = React.useState('')
  const [transactionID, setTransactionID] = React.useState('')
  const [showSuccess, setShowSuccess] = React.useState(false)

  React.useEffect(() => {
    if(showSuccess) {
      const id = setTimeout(() => {
        setShowSuccess(false)
        clearTimeout(id)
      }, 5000)
    }
  }, [showSuccess])

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const addNewAccount = () => {
    fetch('/api/addNewSelfAccount', {
        method:'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ 
            title,
            developer,
            email,
            developerId,
            transactionID
          })
      })
      .then(res => res.json())
      .then(data => {
        console.log(data)
        if(data.result) {
          setShowSuccess(true)
          selfAccountsMobx.update()
          handleClose()
        }
      })
      .catch(err => console.log(err))
  }

  return (
    <React.Fragment>
        <Fab color="primary" aria-label="add" onClick={handleClickOpen} style={{margin:10}} >
            <AddIcon />
        </Fab>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{
          component: 'form',
          onSubmit: (event) => {
            event.preventDefault();
            addNewAccount()
          },
        }}
      >
        <DialogTitle>Добавление нового аккаунта</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Введите данные самофарм-аккаунта
          </DialogContentText>
          <TextField
            autoFocus
            required
            margin="dense"
            id="title"
            name="title"
            label="Название профиля в индиго"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setTitle(e.target.value)}
          />
          <TextField
            autoFocus
            required
            margin="dense"
            id="developer"
            name="developer"
            label="Название профиля разработчика"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setDeveloper(e.target.value)}
          />
          <TextField
            required
            margin="dense"
            id="email"
            name="email"
            label="Email Address"
            type="email"
            fullWidth
            variant="standard"
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            required
            margin="dense"
            id="developerId"
            name="developerId"
            label="Developer ID"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setDeveloperId(e.target.value)}
          />
          <TextField
            required
            margin="dense"
            id="transactionID"
            name="transactionID"
            label="Transaction ID"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setTransactionID(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Отмена</Button>
          <Button type='submit'>Добавить</Button>
        </DialogActions>
      </Dialog>
      <SuccessSnackbar showSuccess={showSuccess} />
    </React.Fragment>
  );
}