import React, {useEffect} from 'react';
import { observer } from 'mobx-react-lite';

function FirstAppName({app}) {  

  const render = () => {
    if(app.goolgePlayLink) return <a href={app.goolgePlayLink} target='_blank'>{app.firstName}</a>
    return <div>{app.firstName}</div>
  }

  return (
    <div>
      {render()}
    </div>
  );
}

export default observer(FirstAppName);
