import React, {useEffect} from 'react';
import { observer } from 'mobx-react-lite';
import ShowComputerTitile from './ShowComputerTitile';
import ChooseComputer from './ChooseComputer';

function Computer({app}) {  

  const render = () => {
    if(app.comp) return <ShowComputerTitile title={app.comp} />
    else return <ChooseComputer app={app} />
}

  return (
    <div>
      { render() }
    </div>
  );

}

export default observer(Computer);
