import React, {useEffect} from 'react';
import { observer } from 'mobx-react-lite';
import AddGooglePlayLink from './AddGooglePlayLink';

function GoolgePlayLink({app}) {  

    const render = () => {
        if(app.goolgePlayLink) return <></>
        return <AddGooglePlayLink app={app} />
    }

    return (
        <div>
            {render()}
        </div>
    );
}

export default observer(GoolgePlayLink);
