import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import AddNewAccount from './addNew/AddNewApp';
import appsMobx from '../../mobx/appsMobx'
import { observer } from 'mobx-react-lite';
import Brend from './tableRow/Brend';
import FirstAppName from './tableRow/FirstAppName';
import TrustAccount from './tableRow/TrustAccount/TrustAccount';
import DevelopmentLink from './tableRow/DevelopmentLink';
import Computer from './tableRow/Computer/Computer';
import Domen from './tableRow/Domain/Domen';
import FirstModeration from './tableRow/FirstModeration/FirstModeration';
import UpdateModeration from './tableRow/UpdateModeration/UpdateModeration';
import TransferAccount from '../APPS/tableRow/TransferAccount/TransferAccount'
import TransferModeration from './tableRow/TransferModeration/TransferModeration';
import GoolgePlayLink from './tableRow/GoolgePlayLink/GoolgePlayLink';


function Apps() {

  return (<>
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Бренд</TableCell>
            <TableCell align="left">Первое название</TableCell>
            <TableCell align="left">Ссылка разработки</TableCell>
            <TableCell align="left">Траст Аккаунт</TableCell>
            <TableCell align="left">Компьютер</TableCell>
            <TableCell align="left">Домен</TableCell>
            <TableCell align="left">Первая модерация</TableCell>
            <TableCell align="left"></TableCell>
            <TableCell align="left">Обновление</TableCell>
            <TableCell align="left">Аккаунт трансфера</TableCell>
            <TableCell align="left">Трансфер</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {appsMobx.list.map((app) => (
            <TableRow
              key={app._id}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
                <TableCell component="th" scope="row">
                    <Brend app={app} />
                </TableCell>
                <TableCell align="left">
                    <FirstAppName app={app} />
                </TableCell>
                <TableCell align="left">
                    <DevelopmentLink developmentLink={app.developmentLink} />
                </TableCell>
                <TableCell align="left">
                    <TrustAccount app={app} />
                </TableCell>
                <TableCell align="left">
                    <Computer app={app} />
                </TableCell>
                <TableCell align="left">
                    <Domen app={app} />
                </TableCell>
                <TableCell align="left">
                    <FirstModeration app={app} />
                </TableCell>
                <TableCell align="left">
                    <GoolgePlayLink app={app} />
                </TableCell>
                <TableCell align="left">
                    <UpdateModeration app={app} />
                </TableCell>
                <TableCell align="left">
                    <TransferAccount app={app} />
                </TableCell>
                <TableCell align="left">
                    <TransferModeration app={app} />
                </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    <AddNewAccount />
    </>
  );
}
export default observer(Apps);

































