import {action, makeObservable, observable} from 'mobx';

class SelfAccountsMobx {
  list = [];

  constructor() {
    makeObservable(this, {
        list: observable,
        change: action,
    });
  }

  update(){
    fetch('/api/getSelfAccounts')
        .then(res => res.json())
        .then(data =>{
          this.change(data)
        })
        .catch(err => console.log(err))
  }

  change(arr) {
    this.list = arr
    console.log(this.list);
    
  }
}

const selfAccountsMobx = new SelfAccountsMobx();

export default selfAccountsMobx;