import React, {useEffect} from 'react';
import { observer } from 'mobx-react-lite';
import ShowTransferAccountTitile from './ShowTransferAccountTitile';
import ChooseTransferAccount from './ChooseTransferAccount';

function TransferAccount({app}) { 

const render = () => {
    if(app.transferAccount) return <ShowTransferAccountTitile id={app.transferAccount} />
    else return app.updateStatus == 'Готов' ? <ChooseTransferAccount app={app} /> : <></>
}

  return (
    <div>
      { render() }
    </div>
  );
}

export default observer(TransferAccount);
