import {action, makeObservable, observable} from 'mobx';

class AppsMobx {
  list = [];

  constructor() {
    makeObservable(this, {
        list: observable,
        change: action,
        update: action
    });
  }

  update(){
    fetch('/api/getApps')
        .then(res => res.json())
        .then(data =>{
          this.change(data)
        })
        .catch(err => console.log(err))
  }

  change(arr) {
    this.list = arr    
  }
}

const appsMobx = new AppsMobx();

export default appsMobx;