import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import appsMobx from '../../../../mobx/appsMobx';

export default function AddGooglePlayLink({app}) {
  const [open, setOpen] = React.useState(false);

  const [goolgePlayLink, setGoolgePlayLink] = React.useState('')

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const addNewAccount = () => {
    fetch('/api/changeGooglePlayLink', {
        method:'PUT',
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ 
          id: app._id,
          goolgePlayLink,
          })
      })
      .then(res => res.json())
      .then(data => {
        console.log(data)
        if(data.result) {
          appsMobx.update()
          handleClose()
        }
      })
      .catch(err => console.log(err))
  }

  return (
    <React.Fragment>
        <Button variant="outlined" onClick={handleClickOpen}>Добавить ссылку</Button>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{
          component: 'form',
          onSubmit: (event) => {
            event.preventDefault();
            addNewAccount()
          },
        }}
      >
        <DialogTitle>Добавление ссылки приложения</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Введите ссылку из Google Play
          </DialogContentText>
          <TextField
            autoFocus
            required
            margin="dense"
            id="goolgePlayLink"
            name="goolgePlayLink"
            label="Ссылка"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setGoolgePlayLink(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Отмена</Button>
          <Button type='submit'>Добавить</Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}