import React, {useEffect, useState} from 'react';
import { observer } from 'mobx-react-lite';
import { Button } from '@mui/material';

function ShowDomainTitile({title}) {
  const [show, setShow] = useState(false)

  return (
    <div>
        {show ? title : <Button variant="outlined" onClick={() => setShow(true)}>Показать</Button>}
    </div>
  );
}

export default observer(ShowDomainTitile);
