import React, {useEffect} from 'react';
import { observer } from 'mobx-react-lite';
import UpdateModerationStatus from './UpdateModerationStatus';

function UpdateModeration({app}) {

  const render = () => {
    if(app.updateStatus == 'Готов') {
      return <div>Готово</div>
    }
    if(app.firstModerationStatus == 'Готов' && app.goolgePlayLink) {
      return <UpdateModerationStatus app={app} />
    }
  }

  return (
    <div>
      {render()}
    </div>
  );
}

export default observer(UpdateModeration);
