import React, {useEffect} from 'react';
import { observer } from 'mobx-react-lite';
import ShowTrustAccountTitile from './ShowTrustAccountTitile';
import ChooseTrustAccount from './ChooseTrustAccount';

function TrustAccount({app}) { 

const render = () => {
    if(app.firstAccount) return <ShowTrustAccountTitile id={app.firstAccount} />
    else return <ChooseTrustAccount app={app} />
}

  return (
    <div>
      { render() }
    </div>
  );
}

export default observer(TrustAccount);
