import React, {useEffect} from 'react';
import { observer } from 'mobx-react-lite';
import ShowDomenTitile from './ShowDomenTitile';
import ChooseDomen from './ChooseDomen';

function Domen({app}) {  

  const render = () => {
      if(app.domen) return <ShowDomenTitile title={app.domen} />
      else return <ChooseDomen app={app} />
  }

  return (
    <div>
      { render() }
    </div>
  );

}

export default observer(Domen);
