import React, {useEffect} from 'react';
import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';
import Header from './header/Header';
import { observer } from 'mobx-react-lite';
import selfAccountsMobx from './mobx/selfAccountsMobx';
import routes from './mobx/routes';
import Home from './pages/HOME/Home.js'
import TrustAccounts from './pages/accounts/TRUST/TrustAccounts.js'
import SelfAccounts from './pages/accounts/SELF/SelfAccounts.js';
import trustAccountsMobx from './mobx/trustAccountsMobx.js';
import Apps from './pages/APPS/Apps.js';
import appsMobx from './mobx/appsMobx.js';
import Loader from './Loader.js';



function App() {  

  useEffect(() => {
      selfAccountsMobx.update()
      trustAccountsMobx.update()
      appsMobx.update()
  }, [])

  return (
    <div className="App">
      {
        selfAccountsMobx.list.length && trustAccountsMobx.list.length && appsMobx.list.length ?
      <>
        <Header />
        {routes.value == 'Home' && <Home />}
        {routes.value == 'TrustAccounts' && <TrustAccounts />}
        {routes.value == 'SelfAccounts' && <SelfAccounts />}
        {routes.value == 'Apps' && <Apps />}
      </>
      :
      <Loader />
      }
    </div>
  );
}

export default observer(App);
