import * as React from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import appsMobx from '../../../../mobx/appsMobx';

function ChooseComputer({app}) {
  const [comp, setComp] = React.useState(null);

  React.useEffect(() => {
    if(comp){
        console.log(comp);
        fetch('/api/changeComputer', {
            method:'PUT',
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ 
              id: app._id,
              comp
              })
          })
          .then(res => res.json())
          .then(data => {
            if(data.result){
                appsMobx.update()
            }
          })
          .catch(err => console.log(err))
    }
  }, [comp])

  const handleChange = (event) => {
    setComp(event.target.value);
  };

  const renderMenuItems = () => {
    return appsMobx.list.reduce((acc, item) => {
        if (acc.includes(item.comp)) {
            return acc;
          }
          return [...acc, item.comp];
    }, [])
    .map(compName => {
        return <MenuItem value={compName}>{compName}</MenuItem>
    })
  }

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">Компьютер</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={comp}
          label="Компьютер"
          onChange={handleChange}
        >
          {renderMenuItems()}
        </Select>
      </FormControl>
    </Box>
  );
}

export default ChooseComputer