import React, {useEffect} from 'react';
import { observer } from 'mobx-react-lite';
import FirstModerationStatus from './FirstModerationStatus';

function FirstModeration({app}) {  

  const render = () => {
      if(app.firstModerationStatus == 'Готов') {
        return <div>Готово</div>
      }
      return app.domen ? <FirstModerationStatus app={app} /> : <></>
  }

  return (
    <div>
        {render()}
    </div>
  );
}

export default observer(FirstModeration);
