import * as React from 'react';
import appsMobx from '../../../../mobx/appsMobx';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';

function ChooseDomen({app}) {
    const [open, setOpen] = React.useState(false);
    const [domen, setDomen] = React.useState('')


  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const addDomain = () => {
    fetch('/api/changeDomain', {
      method:'PUT',
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ 
        id: app._id,
        domen
        })
    })
    .then(res => res.json())
    .then(data => {
      if(data.result){
          appsMobx.update()
          handleClose()
      }
    })
    .catch(err => console.log(err))
  }



  return (
    <React.Fragment>
        <Fab size='small' color="primary" aria-label="add" onClick={handleClickOpen} >
            <AddIcon fontSize="small" />
        </Fab>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{
          component: 'form',
          onSubmit: (event) => {
            event.preventDefault();
            addDomain()
          },
        }}
      >
        <DialogTitle>Добавление домена</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Введите адрес домена для приложения
          </DialogContentText>
          <TextField
            autoFocus
            required
            margin="dense"
            id="domen"
            name="domen"
            label="Домен"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setDomen(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Отмена</Button>
          <Button type='submit'>Добавить</Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}

export default ChooseDomen