import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import SuccessSnackbar from './SuccessSnackbar';
import appsMobx from '../../../mobx/appsMobx';

export default function AddNewAccount() {
  const [open, setOpen] = React.useState(false);
  const [showSuccess, setShowSuccess] = React.useState(false)

  const [brend, setBrend] = React.useState('')
  const [firstName, setFirstName] = React.useState('')
  const [developmentLink, setDevelopmentLink] = React.useState('')

  React.useEffect(() => {
    if(showSuccess) {
      const id = setTimeout(() => {
        setShowSuccess(false)
        clearTimeout(id)
      }, 5000)
    }
  }, [showSuccess])

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const addNewAccount = () => {
    fetch('/api/addNewApp', {
        method:'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ 
          brend,
          firstName,
          developmentLink,
          })
      })
      .then(res => res.json())
      .then(data => {
        console.log(data)
        if(data.result) {
          setShowSuccess(true)
          appsMobx.update()
          handleClose()
        }
      })
      .catch(err => console.log(err))
  }

  return (
    <React.Fragment>
        <Fab color="primary" aria-label="add" onClick={handleClickOpen} style={{margin:10}} >
            <AddIcon />
        </Fab>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{
          component: 'form',
          onSubmit: (event) => {
            event.preventDefault();
            addNewAccount()
          },
        }}
      >
        <DialogTitle>Добавление нового аккаунта</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Введите данные трастового аккаунта
          </DialogContentText>
          <TextField
            autoFocus
            required
            margin="dense"
            id="brend"
            name="brend"
            label="Бренд"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setBrend(e.target.value)}
          />
          <TextField
            autoFocus
            required
            margin="dense"
            id="firstName"
            name="firstName"
            label="Первое название приложения"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setFirstName(e.target.value)}
          />
          <TextField
            required
            margin="dense"
            id="developmentLink"
            name="developmentLink"
            label="Ссылка на разработку"
            type="text"
            fullWidth
            variant="standard"
            onChange={(e) => setDevelopmentLink(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Отмена</Button>
          <Button type='submit'>Добавить</Button>
        </DialogActions>
      </Dialog>
      <SuccessSnackbar showSuccess={showSuccess} />
    </React.Fragment>
  );
}